body {
  margin: 5px;
  padding: 5px;
  font-family: sans-serif;
  text-align: center;
  background-color: #E8E8EE;
}


#app {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  /* height: 100vh; */
  height: 90vh;
  
}

h1 {
  margin-bottom: 40px;
  letter-spacing: 3px;
  font-weight: normal;
}

.calculator-wrapper {
  width: 400px;
  height: 600px;
  position: relative;

}

.row {
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

.clear-btn {
  height: 3em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: lighter;
  font-size: 1.4em;
  background-color: #c5c3cd;
  color: #000000;
  -webkit-flex: 1 1;
          flex: 1 1;
  outline: 1px solid #888888;
  letter-spacing: 2px;
  cursor: pointer;
}
.clear-btn:active {
  box-shadow: inset 0px 0px 80px 0px rgba(0, 0, 0, 0.25);
}

.github {

  font-size: 13px;
  margin: 20px;
  letter-spacing: 2px;
}
.github i {
  padding: 5px;
  color: black;
}
.github i:hover {
  color: #fe9241;
}
.display {
  height: 4em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  font-weight: bold;
  font-size: 1.6em;
  background-color: #000000;
  color: #ffffff;
  padding: 0.5em;
  outline: 1px solid #888888;
  letter-spacing: 1px;
}

.responsive-display {
  display: inline-block;
}

.display .responsive-display {
  padding: 0 20px;
  position: absolute;
  right: 0;
  -webkit-transform-origin: right;
          transform-origin: right;
}
.button-wrapper {
  height: 4em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: lighter;
  font-size: 1.4em;
  background-color: #e0e1e6;
  color: #888888;
  -webkit-flex: 1 1;
          flex: 1 1;
  outline: 1px solid #888888;
  cursor: pointer;
}

.button-wrapper:active {
  box-shadow: inset 0px 0px 80px 0px rgba(0, 0, 0, 0.25);
}

.button-wrapper:hover {
  background-color: #FFCC99;
}

.operator {
  background-color: #fe9241;
  color: #ffffff;
}

.operator:active {
  box-shadow: inset 0px 0px 80px 0px rgba(0, 0, 0, 0.25);
}
#equals {
  background-color: #fe9241;
  color: #ffffff;
}

#equals:hover {
  background-color: #FFCC99;
}
